import React from 'react'
import '../Componenets/Heading5.css'
import divv1 from '../Flipkart-files/divv1.webp'
import div2 from '../Flipkart-files/div2.webp'
import div3 from '../Flipkart-files/div3.webp'


function Heading5() {
    return (
        <>
            <div className="heading5-container">
                <div className="heading5-main-container">
                    <div className="div-img1">
                        <img src={divv1} alt="img1" className='img1' />
                    </div>
                    <div className="div-img2">
                        <img src={div2} alt="img2" className='img1' />
                    </div>
                    <div className="div-img3">
                        <img src={div3} alt="img3" className='img1' />
                    </div>
                </div>
            </div>
        </>
    )
}

export default Heading5
