import React from 'react'
import '../Componenets/Heading3.css'
import heading3 from '../Flipkart-files/heading3.webp'

function Heading3() {
    return (
        <>

            <div className="setimgheading3 newh3">
                <img src={heading3} alt="heading3" className='heading3' />
            </div>


        </>
    )
}

export default Heading3
