import React from 'react'
import '../Componenets/Heading7.css'
import banner1 from '../Flipkart-files/banner1.webp'
import banner2 from '../Flipkart-files/banner2.webp'
import banner3 from '../Flipkart-files/banner3.webp'

function Heading7() {
  return (
    <>
    
    <div className="heading5-container">
                <div className="heading5-main-container">
                    <div className="div-img1">
                        <img src={banner1} alt="banner1" className='img1' />
                    </div>
                    <div className="div-img2">
                        <img src={banner2} alt="banner2" className='img1' />
                    </div>
                    <div className="div-img3">
                        <img src={banner3} alt="banner3" className='img1' />
                    </div>
                </div>
            </div>
    </>
  )
}

export default Heading7
