import React from 'react'
import '../Componenets/Last.css'
import t1 from '../Flipkart-files/t1.webp'
import t2 from '../Flipkart-files/t2.webp'
import t3 from '../Flipkart-files/t3.webp'


function Last() {
    return (
        <>

            <div className="heading5-container">
                <div className="heading5-main-container">
                    <div className="div-img1">
                        <img src={t1} alt="banner1" className='img1' />
                    </div>
                    <div className="div-img2">
                        <img src={t2} alt="banner2" className='img1' />
                    </div>
                    <div className="div-img3">
                        <img src={t3} alt="banner3" className='img1' />
                    </div>
                </div>
            </div>
        </>

    )
}

export default Last
