import React from 'react'
import '../Componenets/Heading9.css'
import h9h1 from '../Flipkart-files/h9h1.webp'
import h9h2 from '../Flipkart-files/h9h2.webp'
import h9h3 from '../Flipkart-files/h9h3.webp'

function Heading9() {
  return (
    <>
    
    <div className="heading5-container">
                <div className="heading5-main-container">
                    <div className="div-img1">
                        <img src={h9h1} alt="banner1" className='img1' />
                    </div>
                    <div className="div-img2">
                        <img src={h9h2} alt="banner2" className='img1' />
                    </div>
                    <div className="div-img3">
                        <img src={h9h3} alt="banner3" className='img1' />
                    </div>
                </div>
            </div>
    </>
  )
}

export default Heading9
